import { HttpClient, HttpParams } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable } from "rxjs";
import { environment } from "src/environments/environment.prod";

@Injectable({
  providedIn: "root",
})
export class RFPApplicationApplicationsService {
  constructor(private http: HttpClient) {}

  getRFPApplications(): Observable<any> {
    const RFPApplicationUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/all`;
    return this.http.get<any[]>(RFPApplicationUrl);
  }

  getRFPApplicationById(params: any): Observable<any> {
    const RFPAnalysisUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/fetchById`;
    return this.http.get<any>(RFPAnalysisUrl, { params });
  }
  getRFPApplicationByTheId(params: any): Observable<any> {
    const RFPAnalysisUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/find-by-id`;
    return this.http.get<any>(RFPAnalysisUrl, { params });
  }

  getRFPApplicationByCode(params: any): Observable<any> {
    const RFPAnalysisUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/fetchByRFPApplicationCode`;
    return this.http.get<any>(RFPAnalysisUrl, { params });
  }

  // allocateApplicationScores(params: any): Observable<any> {
  //   const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/provide/score`;
  //   return this.http.put<any>(updateBillUrl, { params });
  // }

  nominateSupplierByCodes(params: any): Observable<any> {
    const RFPApplicationssUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/nominate`;
    return this.http.put<any[]>(RFPApplicationssUrl, {}, { params });
  }

  getRFPApplicationByRFPCode(params: any): Observable<any> {
    const RFPApplicationssUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/fetchByRfpCode`;
    return this.http.get<any[]>(RFPApplicationssUrl, {params});
  }

  getRFPApplicationByStatus(params: any): Observable<any> {
    const RFPApplicationssUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/fetchByStatus`;
    return this.http.get<any[]>(RFPApplicationssUrl, { params });
  }

  deleteRFPsTemporarily(id: any): Observable<any> {
    const RFPUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/delete`;
    return this.http.delete<any>(RFPUrl, id);
  }


  postRFPApplicationScores(params: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/provide/score`;
    return this.http.put<any>(updateBillUrl, {}, {params})
  }


  getApplicationByFinancialRanking(params: any): Observable<any> {
    const ApplicationMatrixUrl = `${environment.baseUrlProcurement}/api/v1/RFPsApplication/analyse`;
    return this.http.put<any>(ApplicationMatrixUrl,{},{params});
  }
  // ********************************************************************************************************************
}
