import { HttpParams } from '@angular/common/http';
import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription, Subject, takeUntil, BehaviorSubject } from 'rxjs';
import { RFPApplicationApplicationsService } from 'src/app/erp-procurement/data/services/rfp-applications.service';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import { RfpsLookupComponent } from '../../../parameters/pages/lookups/rfps-lookup/rfps-lookup.component';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { ApplicationMatrixService } from 'src/app/erp-procurement/data/services/applications-matrix.service';
import { FilesService, SelectedFiles } from 'src/app/shared/services/files.service';
import Swal from 'sweetalert2';

interface MatrixModel {
  file: string;
  filename: string;
  filetype: string;
  referenceCode: string;
}

@Component({
  selector: 'app-analyse-rfp-applications',
  templateUrl: './analyse-rfp-applications.component.html',
  styleUrls: ['./analyse-rfp-applications.component.scss']
})
export class AnalyseRfpApplicationsComponent implements OnInit {



  displayedColumns: string[] = [
    "rfpName",
    "rfpCode",
    "rfpApplicationCode",
    "supplierCode",
    "isEvaluated",
    "technicalScore",
    "financialScore",
    "finalScore",
    "postedBy",
    "postedTime",
    "status",
    "action",
  ];
  dataSourceRfp!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginatorRfp!: MatPaginator;
  @ViewChild(MatSort) sortRfp!: MatSort;

  subscription!: Subscription;

  accForm: FormGroup;

  isLoading = true;

  destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  rfpId?: any;

  constructor(
    private fb: FormBuilder,
    private route: ActivatedRoute,
    private snackbar: SnackbarService,
    private rfpApplicationApplicationsService: RFPApplicationApplicationsService,
    private dialog: MatDialog,
    private router: Router,
    private applicationMatrixService: ApplicationMatrixService,
    private filesService: FilesService
  ) { }

  ngOnInit(): void {
    this.accForm = this.fb.group({
      rfpNumber: ["", [Validators.required]],
      rfpName: ["", [Validators.required]],
      startDate: ["", [Validators.required]],
      endDate: ["", [Validators.required]],
    });

    const storedTabIndex = localStorage.getItem("activeTabIndex");
    if (storedTabIndex) {
      this.activeTabIndex = +storedTabIndex;
    }

    const storedRfpNumber = localStorage.getItem("rfpNumber");
    const storedRfpAppAnalysisDetails = JSON.parse(
      localStorage.getItem("rfpAppAnalysisDetails")
    );

    console.log("storedRfpAppAnalysisDetails :", storedRfpAppAnalysisDetails);

    if (storedRfpNumber) {
      // Call this.getData() if rfpNumber exists
      this.accForm.patchValue({
        rfpName: storedRfpAppAnalysisDetails.rfpName,
        rfpNumber: storedRfpAppAnalysisDetails.rfpNumber,
        startDate: storedRfpAppAnalysisDetails.startDate,
        endDate: storedRfpAppAnalysisDetails.endDate,
      });
      this.getData();
      this.getMatrixDocs();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  dataRfp: any[] = [];
  data: any;

  getData() {
    this.isLoading = true;

    const params = new HttpParams().set(
      "rfpCode",
      this.accForm.value.rfpNumber
    );

    console.log("this.rfpApplicationCode: ", this.accForm.value.rfpNumber);

    this.rfpApplicationApplicationsService
      .getRFPApplicationByRFPCode(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.rfpAppres: ", res);
          // res = this.sampleData
          // if (res.statusCode === 200) {
          if (res) {
            this.data = res;
            this.isLoading = false;
            this.dataSourceRfp = new MatTableDataSource(this.data.entity);
            this.dataSourceRfp.paginator = this.paginatorRfp;
            this.dataSourceRfp.sort = this.sortRfp;

            console.log("this.data :", this.data);
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              res.message
            );
          }
        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            err.message
          );
        },
        complete: () => {this.isLoading = false; },
      }),
      Subscription;
  }

  viewRFPApplication(data: any) {
    const additionalData = data;
    // const serializedData = JSON.stringify(additionalData);
    let route = "/erp-procurement/rfp-applications/manage-rfp-application";
    this.router.navigate([route], {
      queryParams: { id: data.id, action: "View" },
    });
  }
  validateRFPApplication(data: any) {
    const additionalData = data;
    const serializedData = JSON.stringify(additionalData);
    let route = "/erp-procurement/rfp-applications/manage-rfp-application";
    this.router.navigate([route], {
      queryParams: { id: data.id, action: "Validate" },
    });
  }

  rfpIsSelected = false;
  selectedrfps: any[] = [];
  rfps: any;
  rfpsLookup() {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "1000px";
    dialogConfig.data = {
      action: "viewrfps",
      data: this.rfps,
      selected: this.selectedrfps,
      type: 'APPLICATION'
    };

    const dialogRef = this.dialog.open(RfpsLookupComponent, dialogConfig);

    dialogRef.afterClosed().subscribe((result) => {
      if (result && result.data.length != 0) {
        console.log("result: ", result.data[0]);

        this.accForm.patchValue({
          rfpId: result.data[0].id,
          rfpName: result.data[0].rfpTitle,
          rfpNumber: result.data[0].rfpNumber,
          startDate: result.data[0].startDate,
          endDate: result.data[0].endDate,
        });
        localStorage.setItem("rfpNumber", result.data[0].rfpNumber);
        localStorage.setItem(
          "rfpAppAnalysisDetails",
          JSON.stringify(this.accForm.value)
        );

        this.getData();
        this.getMatrixDocs();
        //this.getSupplierById(result.data[0].id);
        this.rfpIsSelected = true;
      }
    });
  }

  getApplicationsRanking() {
    this.dataSourceRfp = new MatTableDataSource([]);
    this.dataSourceRfp.paginator = this.paginatorRfp;
    this.dataSourceRfp.sort = this.sortRfp;
    this.isLoading = true;

    this.getData();
    this.getMatrixDocs();
  }
  initiateFinancialRanking() {
    this.isLoading = true;

    const storedRfqNumber = localStorage.getItem("rfqNumber");
    const params = new HttpParams().set("rfqCode", storedRfqNumber);
    this.rfpApplicationApplicationsService
      .getApplicationByFinancialRanking(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.downloadBody: ", res);
          if (res.statusCode === 200) {
            this.getData();

          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              res.message
            );
          }
        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            err.message
          );
        },
        complete: () => {
          this.isLoading = false;
        },
      }),
      Subscription;
  }


  onNominateSupplier(row: any) {
    this.isLoading = true;

    const params = new HttpParams()
      .set("rfpApplicationCode", row.rfpApplicationCode)
      .set("rfpCode", this.accForm.value.rfpNumber);

    this.rfpApplicationApplicationsService
      .nominateSupplierByCodes(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.rfpAppres: ", res);
          // res = this.sampleData
          if (res.statusCode === 200) {
            this.snackbar.showNotification("snackbar-success", res.message);

            console.log("this.data :", this.data);
          } else {
            this.snackbar.showNotification("snackbar-danger", res.message);
          }
        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            err.message
          );
        },
        complete: () => {
          this.isLoading = false;
        },
      }),
      Subscription;
  }

  //********************************************************************************************************************** */
  // Manintain Current Tab
  //********************************************************************************************************************** */
  activeTabIndex = 0;
  ngAfterViewInit(): void {
    const storedTabIndex = localStorage.getItem("activeTabIndex");
    if (storedTabIndex) {
      this.activeTabIndex = +storedTabIndex; // Convert to number
    }
  }

  onTabChanged(event: MatTabChangeEvent): void {
    const tabIndex = event.index;
    this.activeTabIndex = tabIndex;
    localStorage.setItem("activeTabIndex", tabIndex.toString());
  }

  //********************************************************************************************************************** */
  // Upload RFP Ranking Matrix
  //********************************************************************************************************************** */

  documents: any[] = [];

  public selectedFiles: SelectedFiles[] = [];
  public isFileLoading = new BehaviorSubject(false);

  matrixModel: MatrixModel = {
    file: "",
    filename: "",
    filetype: "",
    referenceCode: "",
  };

  matrixModelPopulated = false;
  isUploadingFile = false;

  onSelectFile(files): void {
    console.log("file: ", files);
    this.isFileLoading.next(true);
    const file = files[0];

    this.filesService.toBase64(files, this.selectedFiles).subscribe((res) => {
      if (res && res.length > 0) {
        this.isFileLoading.next(false);

        const fileName = file.name;
        const fileParts = fileName.split(".");
        const name = fileParts.slice(0, -1).join(".");
        const extension = fileParts[fileParts.length - 1];

        this.matrixModel = {
          file: res[0].base64,
          filename: name,
          filetype: extension,
          referenceCode: this.accForm.value.rfpNumber,
        };
        this.matrixModelPopulated = true;

        console.log("matrixModel :", this.matrixModel);
      }
    });
  }

  onSubmitMatrix(action: any) {
    console.log("event: ", action);

    if (action === "Add") {
      this.matrixModelPopulated = false;
      this.isUploadingFile = true;
      this.applicationMatrixService
        .addApplicationMatrix(this.matrixModel)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("this.matrixAppres: ", res);

            // res = this.sampleData
            if (res.statusCode == 201) {
            } else {
              this.snackbar.showNotification(
                "snackbar-danger",
                res.message
              );
            }
          },
          error: (err) => {
            this.snackbar.showNotification(
              "snackbar-danger",
              err.message
            );
            this.isUploadingFile = false;

          },
          complete: () => {
            this.getMatrixDocs();
            this.isUploadingFile = false;
          },
        }),
        Subscription;
    }
  }

  downloadMatrixDoc(id: any) {
    console.log("id : ", id);
    const params = new HttpParams().set("id", id);
    this.applicationMatrixService
      .getApplicationMatrixById(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.downloadBody: ", res);
          // res = this.sampleData
          if (res.statusCode === 302) {
            let file = res.entity.file;
            let filename = res.entity.filename;
            let filetype = res.entity.filetype;

            console.log("filename: ", filename);
            console.log("filetype: ", filetype);

            // Download PDF in Chrome etc.
            const source = `${file}`;
            const link = document.createElement("a");
            link.href = source;
            link.download = `${filename}.${filetype}`;
            link.click();
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              res.message
            );
          }
        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            err.message
          );
        },
        complete: () => { },
      }),
      Subscription;
  }

  isFetchingDocuments = false;
  getMatrixDocs() {
    this.isFetchingDocuments = true;
    this.documents = [];
    const params = new HttpParams().set(
      "referenceCode",
      this.accForm.value.rfpNumber
    );

    console.log("getMatrixDocs: ", this.accForm.value.rfpNumber);

    this.applicationMatrixService
      .getApplicationMatrixByCode(params)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("this.getMatrixDocsRes: ", res);
          // res = this.sampleData
          if (res.statusCode === 302) {
            this.documents = res.entity;
            console.log(" this.documents :", this.documents);
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
             res.message
            );
          }
          this.isFetchingDocuments = false;

        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            err.message
          );
          this.isFetchingDocuments = false;
        },
        complete: () => {
          this.isFetchingDocuments = false;
        },
      }),
      Subscription;
  }

  deleteMatrixDocument(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to delete this Document?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Delete Document!",
    }).then((result) => {
      if (result.isConfirmed) {
        const params = new HttpParams().set("id", id);
        this.applicationMatrixService
          .deleteApplicationMatrixTemporarily(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              this.snackbar.showNotification(
                "snackbar-success",
                "Matrix Deleted Successfully"
              );
            },
            error: (err) => {
              this.snackbar.showNotification(
                "snackbar-danger",
                err.message
              );
            },
            complete: () => {
              this.getMatrixDocs();
            },
          }),
          Subscription;
      }
    });
  }

  //********************************************************************************************************************** */
}
