import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllRfpApplicationsComponent } from './pages/all-rfp-applications/all-rfp-applications.component';
import { ManageRfpApplicationsComponent } from './pages/manage-rfp-applications/manage-rfp-applications.component';
import { AnalyseRfpApplicationsComponent } from './pages/analyse-rfp-applications/analyse-rfp-applications.component';
import { RoutePrivilegeGuard } from '../../data/services/_AccessControlAuthGuard.service';

const routes: Routes = [
  {
    path: "all-rfp-appications",
    component: AllRfpApplicationsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["All Rfp Applications"] },
  },

 
  
  {
    path: "manage-rfp-application",
    component: ManageRfpApplicationsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["Manage Rfp Application"] },
  },
  {
    path: "analyse-rfp-applications",
    component: AnalyseRfpApplicationsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["Analyse Rfp Applications"] },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RecieverfpapplicationRoutingModule { }
